import { Modal } from "@nextui-org/react";
import { DevBox } from "components/box";
import HEXADOImage from "components/image";
import styled from "styled-components";
import { SharedImage } from "constants/image-constants";
import { Row } from "components/layout";
import { Button } from "components/button";

const { HEXADO_ARCHIVE_9 } = SharedImage;

export default function DetailModal({ visible, closeHandler }) {
    return (
        <Modal
            closeButton
            blur
            aria-labelledby="modal-title"
            open={visible}
            onClose={closeHandler}
            width="1050px"
        >
            <Modal.Header>
            </Modal.Header>
            <Modal.Body>
                <ContentGrid>
                    <HEXADOImage src={HEXADO_ARCHIVE_9} className="hexado-detail-img" />
                    <DevBox style={{ height: "min-content" }}>
                        <ModalContent>
                            <h2>CIDIOT</h2>
                            <h3>About</h3>
                            <p>
                                Hades drop is our establishing drop, the first out of four. After the mint, holders will be able to immediately trade their NFTs. This shall be interesting. Weâ€™ll see how our various economic attributes and utilities affect our base price, economy, trade volume, and so on!
                                It doesn't end here, this is just the first out of four!
                            </p>
                            <Row gap="15px">
                                <h3>Goal</h3>
                                <p>Kill other players</p>
                            </Row>
                            <Row gap="15px">
                                <h3>Size</h3>
                                <h3>M</h3>
                            </Row>
                            <Row gap="15px">
                                <h3>Size</h3>
                                <h3>Shoota</h3>
                            </Row>
                            <Row className="hexado-modal-button-wrapper">
                                <Button style={{ width: "100%" }}>
                                    Close
                                </Button>
                            </Row>
                        </ModalContent>
                    </DevBox>
                </ContentGrid>
            </Modal.Body>
            <Modal.Footer>
            </Modal.Footer>
        </Modal>
    )
}

const ModalContent = styled.div`
    color:white;
    background-color:#101027;
    padding:0px 20px 20px 20px;
    font-family:Open Sans;
    p {
        font-size: 14px;
        line-height:24px;
    }
    .hexado-modal-button-wrapper {
        padding-top:30px;
    }
`

const ContentGrid = styled.div`
    display:grid;
    grid-template-columns: 1fr 350px;
    gap:50px;
    grid-auto-rows:min-content;
    align-items:flex-start;
    .hexado-detail-img{
        min-width:250px;
    }
    @media screen and (max-width:750px) {
    }
    @media screen and (max-width:700px) {
        grid-template-columns: 1fr;
    }
`