const Wrapper = styled.div`
    background-color:#03031B;
    color:white;
    display:flex;
    flex-direction:row;
    justify-content:center;
    align-items:center;
    padding:50px 20px;
    font-family:Open Sans;
    .hexado-video-game-title{
        max-width:1200px;
    }
    .rc-tabs-nav-list {
        display:flex;
        flex-direction:row;
        justify-content:center;
        gap:20px;
        font-family:Open sans;
        font-weight:700;
        cursor:pointer;
        padding-top:50px;
    }
    .rc-tabs-tab{
        background-color:#101027;
        padding:10px;
        border-radius:1000px;
    }
    .rc-tabs-tab-active{
        background: linear-gradient(90deg, #0073FF 0%, #DB00FF 100%);
        padding:10px;
        border-radius:1000px;
    }
    .rc-tabs-ink-bar{
        display:none;
    }
    .hexado-button-wrapper{
        padding-top:30px;
        .hexado-detail-button{
            width:100%;
        }
    }
    .hexado-card-content {
        padding: 0px  20px 20px 20px;
        background-color:#101027;
        p{
            font-size:14px;
            line-height:24px;
        }
    }
    .hexado-tab-content {
        display:grid;
        grid-template-columns:repeat(4, minmax(0, 1fr));
        gap:30px;
        .hexado-level-image{
            img{
                width:100%;
                height:250px;
                object-fit:cover;
            }
        }
        @media screen and (max-width:1024px) {
            grid-template-columns:repeat(3, minmax(0, 1fr));
        }
        @media screen and (max-width:780px) {
            grid-template-columns:repeat(2, minmax(0, 1fr));
        }
        @media screen and (max-width:560px) {
            grid-template-columns:repeat(1, minmax(0, 1fr));
        }
    }
    .hexado-level {
        border-radius:100px;
        position:absolute;
        right:0px;
        transform:translateY(-30px);
    }
    .hexado-level-content {
        background-color:#101027;
        padding:10px 17px;
        border-radius:100px;
        color:#FFC300;
        font-weight:800;
    }
    .hexado-level-header{
        position:relative;
    }
    .hexado-cashback-collapse{
        padding-top:50px;
    }
`